@font-face {
  src: url("DMSans-Bold.306cbdd3.ttf"), url("DMSans-Bold.306cbdd3.ttf");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  src: url("DMSans-Medium.b8d1a1bf.ttf"), url("DMSans-Medium.b8d1a1bf.ttf");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  src: url("DMSans-Regular.331f979e.ttf"), url("DMSans-Regular.331f979e.ttf");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  src: url("DMSans-Italic.ed07c4fe.ttf"), url("DMSans-Italic.ed07c4fe.ttf");
  font-weight: 400;
  font-display: swap;
  font-style: italic;
}

:root {
  --logo-theme-light: #000;
  --logo-theme-dark: #f3f3f3;
  --dark-theme-background: #202024;
  --light-theme-background: #f6f6f6;
  --text-dark-color: #111;
  --text-light-color: #b4afaf;
  --text-white-color: #fff;
  --accent-color1: #4f2ee8;
  --accent-color2: #eac645;
  --accent-color3: #fff;
  --scrollbar-light-color: #e0e0e0;
  --scrollbar-dark-color: #1f1d1d;
  --main-font: "DM Sans";
  --transition: 500ms cubic-bezier(.4, 0, .2, 1);
}

html {
  min-width: 200px;
  overflow-y: scroll;
}

body {
  letter-spacing: .02em;
  background-color: var(--light-theme-background);
  font-family: DM Sans, sans-serif;
}

.list {
  list-style: none;
}

.link {
  font-family: inherit;
  text-decoration: none;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--main-font), sans-serif;
  margin-top: 0;
  margin-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  display: none;
}

.container {
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

.sidebar {
  flex-direction: column;
  row-gap: 40px;
  display: flex;
}

@media screen and (max-width: 767.8px) {
  .container {
    max-width: 375px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1439.8px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  .sidebar {
    flex-direction: row;
    justify-content: space-between;
    margin-left: 0;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    flex-direction: row;
    padding-left: 24px;
    padding-right: 24px;
  }

  .sidebar {
    flex-direction: column;
    row-gap: 86px;
  }
}

.header {
  z-index: 100;
  background-color: var(--accent-color3);
  transition: background-color var(--transition);
  border: 1.5px solid #111;
  border-radius: 0 0 8px 8px;
  position: sticky;
  top: 0;
  right: 0;
}

.header .container {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.logo-and-navigation {
  align-items: center;
  display: flex;
}

.burger-logo {
  stroke: #000;
}

.logo-link {
  letter-spacing: -.04em;
  color: var(--logo-theme-light);
  align-items: center;
  gap: 8px;
  font-weight: 600;
  display: flex;
}

.switch-burger-login {
  align-items: center;
  display: flex;
}

.mobile-home-link {
  letter-spacing: -.04em;
  color: var(--accent-color3);
  transition: background-color var(--transition), font-weight var(--transition);
  border-radius: 36px;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
  display: inline-flex;
}

.mobile-home-link:hover, .mobile-home-link:focus {
  background-color: var(--accent-color2);
  transition: font-weight var(--transition);
  font-weight: 700;
}

.mobile-shopping-link {
  letter-spacing: -.04em;
  color: var(--accent-color3);
  transition: background-color var(--transition), font-weight var(--transition);
  border-radius: 36px;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
  display: inline-flex;
}

.mobile-shopping-link:hover, .mobile-shopping-link:focus {
  background: var(--accent-color2);
  font-weight: 700;
}

.mobile-shopping-bag-icon {
  fill: var(--accent-color3);
  transition: var(--transition);
}

.mobile-home-link.mobile-selected, .mobile-shopping-link.mobile-selected, .mobile-shopping-bag-icon.mobile-selected {
  background-color: var(--accent-color2);
  color: var(--text-dark-color);
  fill: var(--text-dark-color);
  font-weight: 700;
}

.is-hidden, .is-hidden-modal {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.switch {
  height: 20px;
  margin-left: auto;
  display: inline-block;
  position: relative;
}

.switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background-color: var(--accent-color3);
  transition: all .4s;
  position: absolute;
  top: 1px;
  bottom: 1px;
  left: 1px;
}

input:checked + .slider, input:checked + .slider {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.shopping-link {
  position: relative;
}

.count-shopping-list {
  background-color: var(--accent-color1);
  text-align: center;
  width: 17px;
  height: 17px;
  border-radius: 50%;
  padding: 2px;
  display: none;
  position: absolute;
  bottom: 0;
  right: 8px;
}

.count-length {
  color: var(--accent-color3);
  font-size: 10px;
  display: inline-flex;
  transform: translateY(-3px);
}

.home-link.selected, .shopping-link.selected {
  background-color: var(--accent-color2);
  font-weight: 700;
}

@media screen and (max-width: 189.8px) {
  .header .container {
    flex-direction: column;
    justify-content: center;
    row-gap: 5px;
  }

  .logo-link {
    font-size: 14px;
  }
}

@media screen and (min-width: 190px) and (max-width: 249.8px) {
  .logo-link {
    font-size: 13px;
  }
}

@media screen and (max-width: 249.8px) {
  .switch {
    width: 30px;
    height: 15px;
  }

  .slider:before {
    height: 14px;
    width: 14px;
    top: .5px;
  }

  .burger-logo {
    width: 20px;
    height: 20px;
  }

  .switch {
    width: 28px;
    margin-right: 10px;
  }

  input:checked + .slider:before {
    transform: translateX(14px);
  }
}

@media screen and (min-width: 250px) {
  .burger-logo {
    width: 28px;
    height: 28px;
  }

  .switch {
    width: 40px;
    margin-right: 14px;
  }

  input:checked + .slider:before {
    transform: translateX(20px);
  }
}

@media screen and (max-width: 767.8px) {
  .header {
    padding-top: 17px;
    padding-bottom: 16px;
  }

  .navigation {
    display: none;
  }

  .mobile-navigation {
    margin-top: 126px;
    margin-left: 108px;
  }

  .mobile-menu-container {
    height: 100%;
  }

  .burger-menu {
    background-color: rgba(0, 0, 0, 0);
    border: none;
    padding: 0;
    line-height: 0;
  }

  .mobile-menu {
    z-index: 10;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0);
    background-image: url("mobile_menu_background@1x.d044c27d.png"), radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
    background-position: 100% 100%;
    background-repeat: no-repeat;
    background-size: contain;
    background-attachment: scroll, scroll;
    background-origin: padding-box, padding-box;
    background-clip: border-box, border-box;
    transition: all .3s linear;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
}

@media screen and (max-width: 767.8px) and (min-device-pixel-ratio: 2), screen and (max-width: 767.8px) and (min-resolution: 192dpi), screen and (max-width: 767.8px) and (min-resolution: 2x) {
  .mobile-menu {
    background-color: rgba(0, 0, 0, 0);
    background-image: url("mobile_menu_background@2x.556da661.png"), radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
    background-position: 100% 100%;
    background-repeat: no-repeat;
    background-size: contain;
    background-attachment: scroll, scroll;
    background-origin: padding-box, padding-box;
    background-clip: border-box, border-box;
  }
}

@media screen and (max-width: 767.8px) {
  .icon-close-mobile-menu, .icon-burger-mobile-menu {
    transition: opacity var(--transition);
  }
}

@media screen and (max-width: 767.8px) {
  .user-login {
    display: none;
  }
}

@media screen and (max-width: 767.8px) {
  .user-sign-up-mobile {
    width: 184px;
    background: var(--accent-color1);
    cursor: pointer;
    letter-spacing: -.04em;
    color: var(--accent-color3);
    transition: var(--transition);
    border: 1.5px solid #111;
    border-radius: 18px;
    justify-content: space-between;
    align-items: center;
    margin-top: 84px;
    padding: 13px 14px 12px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.29;
    display: flex;
  }
}

@media screen and (max-width: 767.8px) {
  .icon-arrow-right {
    stroke: var(--accent-color2);
  }
}

@media screen and (max-width: 767.8px) {
  .user-login-mobile:hover, .user-login-mobile:focus {
    background-color: var(--accent-color3);
    color: var(--text-dark-color);
  }
}

@media screen and (max-width: 767.8px) {
  .loged-in-wrapper {
    width: 100%;
    padding-top: 84px;
    padding-bottom: 20px;
  }
}

@media screen and (max-width: 767.8px) {
  .user-login-mobile {
    letter-spacing: -.02em;
    color: var(--accent-color3);
    align-items: center;
    font-size: 22px;
    font-weight: 700;
    line-height: 1.32;
    display: flex;
  }
}

@media screen and (max-width: 767.8px) {
  .icon-user-wrapper-mobile {
    width: 37px;
    height: 37px;
    background: var(--light-theme-background);
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    margin-right: 9px;
    display: inline-flex;
  }
}

@media screen and (max-width: 767.8px) {
  .nav-mobile-wrap {
    max-width: 150px;
    margin-top: 126px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (max-width: 767.8px) {
  .nav-item-mobile:not(:last-child) {
    margin-bottom: 8px;
  }
}

@media screen and (max-width: 767.8px) {
  .shopping-link-mobile {
    align-items: center;
    gap: 4px;
    display: flex;
  }
}

@media screen and (max-width: 767.8px) {
  .shopping-bag-icon-mobile {
    fill: var(--accent-color3);
    transition: var(--transition);
  }
}

@media screen and (max-width: 767.8px) {
  .home-link-mobile, .shopping-link-mobile {
    font-family: var(--main-font), sans-serif;
    letter-spacing: -.04em;
    text-transform: uppercase;
    color: var(--accent-color3);
    border-radius: 36px;
    padding: 7px 16px;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 1.29;
    transition: all .4s linear;
  }
}

@media screen and (max-width: 767.8px) {
  .shopping-link-mobile:hover, .home-link-mobile:hover, .shopping-link-mobile:focus, .home-link-mobile:focus {
    color: var(--text-dark-color);
    background-color: var(--light-accent-color-2);
  }
}

@media screen and (max-width: 767.8px) {
  .shopping-link-mobile:hover .shopping-bag-icon-mobile, .shopping-link-mobile:focus .shopping-bag-icon-mobile {
    fill: var(--text-dark-color);
  }
}

@media screen and (max-width: 767.8px) {
  .log-out-mobile {
    min-width: 118px;
    background: var(--accent-color3);
    cursor: pointer;
    color: var(--text-dark-color);
    letter-spacing: -.04em;
    color: var(--text-dark-color);
    border: 1.5px solid #111;
    border-radius: 18px;
    align-items: center;
    gap: 4px;
    margin-top: 382px;
    padding: 12px 24px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.29;
    display: flex;
  }
}

@media screen and (min-width: 768px) {
  .header {
    padding-top: 11px;
    padding-bottom: 10px;
  }

  .logo-and-navigation {
    column-gap: 40px;
  }

  .navigation {
    display: block;
  }

  .nav-list {
    align-items: center;
    gap: 8px;
    display: flex;
  }

  .switch {
    margin-right: 24px;
  }

  .burger-menu, .mobile-menu {
    display: none;
  }

  .home-link {
    letter-spacing: -.04em;
    color: var(--text-dark-color);
    transition: background-color var(--transition), font-weight var(--transition);
    border-radius: 36px;
    padding: 8px 16px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.28;
    display: inline-flex;
  }

  .home-link:hover, .home-link:focus {
    background-color: var(--accent-color2);
    transition: font-weight var(--transition);
    font-weight: 700;
  }

  .shopping-link {
    letter-spacing: -.04em;
    color: var(--text-dark-color);
    transition: background-color var(--transition), font-weight var(--transition);
    border-radius: 36px;
    align-items: center;
    gap: 8px;
    padding: 7px 16px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.28;
    display: inline-flex;
  }

  .shopping-link:hover, .shopping-link:focus {
    background: var(--accent-color2);
    font-weight: 700;
  }

  .shopping-bag-icon {
    line-height: 0;
  }

  .user-login {
    background: var(--accent-color1);
    cursor: pointer;
    letter-spacing: -.04em;
    color: var(--accent-color3);
    transition: background-color var(--transition);
    border: 1.5px solid #111;
    border-radius: 18px;
    justify-content: space-between;
    align-items: center;
    column-gap: 90px;
    padding: 14px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.29;
    display: flex;
  }

  .user-login:hover, .user-login:focus {
    background-color: #37209d;
  }

  .icon-arrow-right {
    stroke: var(--accent-color2);
  }

  .icon-user-wrapper {
    width: 37px;
    height: 37px;
    background: var(--light-theme-background);
